import React, { Suspense } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import AuthRoute from "./components/AuthRoute";
import UserRoute from "./components/UserRoute";

const Login = React.lazy(() => import("./pages/Login"));
const Painel = React.lazy(() => import("./pages/Painel"));
const DashboardCompras = React.lazy(() => import("./pages/DashboardCompras"));
const AprovarPedidos = React.lazy(() => import("./pages/AprovarPedidos"));
const FreteVendas = React.lazy(() => import("./pages/FreteVendas"));
const Configuracoes = React.lazy(() => import("./pages/Configuracoes"));
const MinMax = React.lazy(() => import("./pages/MinMax"));
const CalculoMargem = React.lazy(() => import("./pages/CalculoMargem"));
const NovaProspeccao = React.lazy(() => import("./pages/NovaProspeccao"));
const Prospeccoes = React.lazy(() => import("./pages/Prospeccoes"));
const Produtos = React.lazy(() => import("./pages/Produtos"));
const Disponibilidade = React.lazy(() => import("./pages/Disponibilidade"));
const Etiquetas = React.lazy(() => import("./pages/Etiquetas"));
const TabelaComponex = React.lazy(() => import("./pages/TabelaComponex"));

const deployAuth = true;

export default function Routes() {
  return (
    <BrowserRouter>
      <Suspense fallback={<></>}>
        <Switch>
          {deployAuth && (
            <>
              <Route exact path="/componex" component={TabelaComponex} />

              <AuthRoute
                exact
                path="/"
                requireAuth={false}
                redirect="/painel"
                component={Login}
              />

              <AuthRoute
                exact
                path="/configuracoes"
                requireAuth={true}
                redirect="/"
                component={Configuracoes}
              />
              <AuthRoute
                exact
                path="/painel"
                requireAuth={true}
                redirect="/"
                component={Painel}
              />

              <AuthRoute
                exact
                path="/dashcompras"
                requireAuth={true}
                redirect="/"
                component={DashboardCompras}
              />

              <AuthRoute
                exact
                path="/fretevendas"
                requireAuth={true}
                redirect="/"
                component={FreteVendas}
              />

              <AuthRoute
                exact
                path="/relatoriominmax"
                requireAuth={true}
                redirect="/"
                component={MinMax}
              />

              <AuthRoute
                exact
                path="/calculomargem"
                requireAuth={true}
                redirect="/"
                component={CalculoMargem}
              />

              <AuthRoute
                exact
                path="/expedicao/aprovarpedidos"
                requireAuth={true}
                redirect="/"
                component={AprovarPedidos}
              />

              <AuthRoute
                exact
                path="/expedicao/etiquetas"
                requireAuth={true}
                redirect="/"
                component={Etiquetas}
              />

              <AuthRoute
                exact
                path="/prospeccao"
                requireAuth={true}
                redirect="/"
                component={Prospeccoes}
              />

              <AuthRoute
                exact
                path="/prospeccao/criar"
                requireAuth={true}
                redirect="/"
                component={NovaProspeccao}
              />

              {/* <AuthRoute
                exact
                path="/prospeccao/listar"
                requireAuth={true}
                redirect="/"
                component={Prospeccoes}
              /> */}

              <UserRoute
                exact
                path="/prospeccao/listar"
                requireAuth={true}
                redirect="/"
                component={Prospeccoes}
                users={[
                  "e349fdc1-ce22-4707-8d6b-af06abab5386", // Rhenan
                  "471cd426-4050-4118-8ae7-b449f2ad7d40", // Felipe
                  "dbb73324-e0ea-447a-ad7f-4dd2ecbb649c", // Hesley
                  "ee954c5b-6a1d-40be-b6f3-5bfde95df3ce", // Katia
                  "caa86543-b722-4a2f-ba4d-0ff84089b170", // Lucas
                ]}
              />

              <AuthRoute
                exact
                path="/produtos"
                requireAuth={true}
                redirect="/"
                component={Produtos}
              />

              <AuthRoute
                exact
                path="/compras/disponibilidade"
                requireAuth={true}
                redirect="/"
                component={Disponibilidade}
              />
            </>
          )}
        </Switch>
      </Suspense>
    </BrowserRouter>
  );
}
